  font-family: 'Droid Sans Mono';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Droid Sans Mono Regular '),
    local('Droid Sans Mono-Regular'),
    url('./files/droid-sans-mono-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/droid-sans-mono-latin-400.woff') format('woff'); /* Modern Browsers */
}

